const mapStateToProps = (state) => {
    return {
        isInfoPage: state.app.isInfoPage,
        isInViewport: state.app.isInViewport
    }
  }



class InfoPage extends React.PureComponent {

  componentDidMount(){

    this.props.isInfoPage(true);

  }


  render(){

    return this.props.pageContext.locale === "en-gb" ?

    <EnglishInfo/>

    :

    <GermanInfo/>

  }
}


export default connect(
    mapStateToProps,
    dispatch => ({ isInfoPage: operator => dispatch(toggleIsInfoPage(operator)) }),
  )(InfoPage);